.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 0px;
}

.App-header {
  background-color: #222;
  height: 40px;
  padding: 10px;
  color: white;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

rect.overlay {
  opacity: 0;
}

rect.selection {
  fill: #fe9922;
  opacity: 0.5;
}

rect.handle {
  fill: #fe9922;
  opacity: 0.25;
}

path.countries {
  stroke-width: 1;
  stroke: #75739f;
  fill: #5eafc6;
}

body,
html,
#root {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  position: relative;
}

* {
  box-sizing: border-box;
}

#content {
  position: absolute;
  left: 40px;
}

#options {
  float: left;
}

.techniques {
  color: white;
  border: solid 1px white;
  padding: 10px;
}

#suboptions {
  color: white;
  float: left;
  margin-left: 20px;
}

#buttons {
  margin-top: 20px;
}
